h2 {
    text-align: center;
}

.searchWrapper {
    display: block;
    margin: 20px auto;
    display: flex;
    justify-content: center;
    width: 80%;
}

.employeeCard {
    display: block;
    width: 80%;
    color: #000;
    margin: 0 auto;
    position: relative;
    background-color: #ccc;
    padding: 10px;
    box-sizing: border-box;
    display: flex;
    border-bottom: 1px solid black;
    justify-content: space-between;  
}

.employeeTitles {
    display: block;
    width: 80%;
    color: #fff;
    margin: 0 auto;
    position: relative;
    background-color: #484747;
    padding: 10px;
    box-sizing: border-box;
    display: flex;
    border-bottom: 1px solid black; 
    justify-content: space-between;  
}

h3 {
    font-family: 'Roboto Condensed', sans-serif;
    padding: 0 10px;
    text-align: center;
    box-sizing: border-box;
    justify-content: space-around; 
}

h5 {
    font-family: 'Roboto Condensed', sans-serif;
    color: white;
    font-size: 1em;
    text-align: center;
    margin: 0 auto;
    line-height: 1em;
    position: relative;
    top: 50%;
    transform: translateY(-50%);
    text-transform: uppercase;
    letter-spacing: 2px;
}

p {
    padding: 20px;
    box-sizing: border-box;
    justify-content: space-between;  
} 